<template>
  <el-form
    class="form"
    :inline="true"
    @submit.native.prevent="onSubmit"
    :model="form"
    label-width="100px"
  >
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item><a @click="back">分享列表</a></el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24" v-loading="loading">
      <el-col>
        <el-form-item label="名称">
          <el-input v-model="form.name" placeholder="请输入名称"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="排序">
          <el-input v-model="form.sort" placeholder="请输入内容"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="奖励">
          <el-input
            v-model="form.money"
            type="number"
            step="1"
            placeholder="请输入肥料数量"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否开启">
          <el-select
            size="small"
            v-model="form.status"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="图片">
          <el-upload
            :limit="1"
            :action="'/api/api/upload?ssid=' + ssid"
            :on-success="fileSuccess"
            :file-list="file"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
            <div slot="tip" style="color: red" class="el-upload__tip">
              {{ imageRemark }}
            </div>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="截图示例">
          <el-upload
            :limit="1"
            :action="'/api/api/upload?ssid=' + ssid"
            :on-success="imageSuccess"
            :file-list="image"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
            <div slot="tip" style="color: red" class="el-upload__tip">
              尺寸:宽720px 高:1280px 格式:png、jpg、jpeg、gif
            </div>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      loading: false,
      form: {
        status: 'Y',
        sort: 0,
        url: '/',
        thumb: '',
      },
      image: [],
      file: [],
      type: 'banner',
      imageRemark: '尺寸:宽750px 高:300px 格式:png、jpg、jpeg、gif',
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    const id = this.$router.history.current.params.id

    if (id) {
      this.loading = true
      Fetch('/user/jz/view', { id }).then(r => {
        this.form = r.data.view

        if (r.data.view.thumb) {
          this.file = [
            {
              name: r.data.view.thumb,
              url: r.data.view.thumb,
            },
          ]
        }
        if (r.data.view.image) {
          this.image = [
            {
              name: r.data.view.image,
              url: r.data.view.image,
            },
          ]
        }
        this.loading = false
      })
    }
  },
  methods: {
    back() {
      this.$router.back()
    },
    imageSuccess(res) {
      this.form.image = res.data.file
    },
    fileSuccess(res) {
      this.form.thumb = res.data.file
    },
    onSubmit() {
      this.loading = true
      const id = this.$router.history.current.params.id
      Fetch('/user/jz/' + (id ? 'update' : 'create'), {
        id: this.$router.history.current.params.id,
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
          this.$router.go(-1)
        })
        .catch(() => {
          Message.error('操作失败')
          this.loading = false
        })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
